/* eslint-disable */
/**
 * Snap JavaScript Client
 * Snap by Midtrans
 */

// Some browsers (mainly IE) does not have this property, so we need to build it manually...
if (!window.location.origin) {
  // @ts-ignore
  window.location.origin = window.location.protocol + '//' + window.location.hostname +
    (window.location.port ? ':' + window.location.port : '');
}

import config from 'config'; // eslint-disable-line import/no-unresolved
const BASE_URL = config.snap_host;
const APP_PATH = config.root_path;
let parentStyle;

function isSnapUrl(src) {
  return ((src.indexOf(BASE_URL) > -1 || src.indexOf('veritrans.co.id') > -1) &&
          (src.indexOf('snap.js') > -1 || src.indexOf('snap.min.js') > -1));
}

function getSnapScript() {
  let snapScript;
  const scripts = document.getElementsByTagName('script');

  for (let i = 0; i < scripts.length; i++) {
    const src = scripts[i].src;

    if (isSnapUrl(src)) {
      snapScript = scripts[i];
    }
  }

  return snapScript;
}

function getScriptLoadDuration() {
  try {
    if (window.performance && window.performance.getEntries) {
      const entries = window.performance.getEntries();
      let entry = null;
      for (let i = 0; i < entries.length; i++) {
        const name = entries[i].name;
        if (isSnapUrl(name)) {
          entry = entries[i];
          break;
        }
      }

      if (entry) {
        return entry.duration;
      }
    }
  } catch (e) {
    // ignore
  }
  return null;
}

export class SnapApp {
  /**
   * @param clientKey {string}
   */
  constructor(clientKey) {
    const pageUrl = BASE_URL + APP_PATH + '/v1/pay' +
      '?origin_host=' + window.location.origin +
      (process.env.APP_JS_DIGEST ? '&digest=' + process.env.APP_JS_DIGEST : '') +
      (clientKey ? '&client_key=' + clientKey : '') + '#/';

    const iframe = document.createElement('iframe');

    iframe.src = pageUrl;
    iframe.id = 'snap-midtrans';
    iframe.style.display = 'none';

    this.pageUrl = pageUrl;
    this.clientKey = clientKey;
    this.iframe = iframe;
    this.attached = false;
    this.embedded = false;
  }

  goHome() {
    this.iframe.src = this.pageUrl;
    this.ensureAttached();
    return this;
  }

  postMessage(data) {
    this.ensureAttached();
    this.iframe.contentWindow.postMessage(data, BASE_URL);
    return this;
  }

  hide() {
    this.iframe.style.display = 'none';
    SnapApp.setParentStyle(false, { disableScroll: false });
    this.goHome();

    this.detach();
    return this;
  }

  showPopup() {
    this.ensureAttached();
    this.iframe.style.cssText = null;
    this.iframe.style.display = 'block';
    this.iframe.style.position = 'fixed';
    this.iframe.style.width = '100%';
    this.iframe.style.height = '100%';
    this.iframe.style.top = 0;
    this.iframe.style.left = 0;
    this.iframe.style.zIndex = 999999;
    this.iframe.style.border = 0;

    SnapApp.setParentStyle(true, { disableScroll: true });
    return this;
  }

  showEmbedded() {
    this.ensureAttached();
    this.iframe.style.cssText = null;
    this.iframe.style.display = 'block';
    this.iframe.style.height = 'inherit';
    this.iframe.style.width = 'inherit';
    this.iframe.style.border = 'none';
    this.iframe.style.minHeight = '560px';
    this.iframe.style.minWidth = '320px';
    this.iframe.style.borderRadius = 'inherit';

    SnapApp.setParentStyle(true, { disableScroll: false });
    return this;
  }

  isVisible() {
    return this.iframe.style.display !== 'none';
  }

  ensureAttached() {
    if (!this.attached) {
      this.attach();
    }
  }

  attach(embedId) {
    if (embedId) {
      this.detach();

      const snapContainer = document.getElementById(embedId);
      if (snapContainer) {
        this.iframe.name = `embed_${+new Date()}`;
        snapContainer.appendChild(this.iframe);
        this.embedded = true;
        this.attached = true;
      }
    } else if (
      !this.attached && document.body &&
      ['interactive', 'complete'].includes(document.readyState)
    ) {
      this.iframe.name = `popup_${+new Date()}`;
      document.body.appendChild(this.iframe);
      this.attached = true;
    }
  }

  detach() {
    if (this.attached) {
      this.iframe.parentNode.removeChild(this.iframe);
      this.attached = false;
      this.embedded = false;
    }
  }

  static setParentStyle(show, options) {
    if (show) {
      if (!parentStyle) {
        parentStyle = {
          position: SnapApp.isMobile() ? document.body.style.position : null,
          overflow: document.body.style.overflow,
          width: document.body.style.width,
          height: document.body.style.height,
          pageXOffset: window.pageXOffset,
          pageYOffset: window.pageYOffset
        };
      }
      if (options.disableScroll) {
        document.body.style.overflow = 'hidden';
        document.body.style.width = '100vw';
        document.body.style.height = '100vh';
        if (SnapApp.isMobile()) {
          // Previously we force position:fixed only for iOS Webkits.
          // The bug occured on Android, so now we handle this for all mobile view of Snap & VT-Web.
          document.body.style.position = 'fixed';
        }
      }
      window.scroll(0, 0);
    } else if (parentStyle) {
      if (SnapApp.isMobile()) {
        document.body.style.position = parentStyle.position;
      }
      document.body.style.overflow = parentStyle.overflow;
      document.body.style.width = parentStyle.width;
      document.body.style.height = parentStyle.height;
      window.scroll(parentStyle.pageXOffset, parentStyle.pageYOffset);
    }
  }

  static isMobile() {
    // Snap mobile view break point
    return window.innerWidth < 568;
  }
}


const UnInitialized = 'unInitialized',
  Initialized = 'initialized',
  Loading = 'loading',
  PopupInView = 'PopupInView';

let currentState = UnInitialized;
/**
 * @param name {string}
 * @param action {function(): any}
 * @param sourceStates {string[]}
 * @param targetState {string}
 */
function defineTransition(name, action, sourceStates, targetState) {
  return function () {
    if (sourceStates.indexOf(currentState) === -1) {
      throw new Error(`snap.${name} is not allowed to be called in this state. Invalid state transition from ${currentState} to ${targetState}`);
    }
    const result = action.apply(null, arguments);
    currentState = targetState;
    return result;
  };
}

/** @type {SnapApp} */
let snapApp;
let queuedToken;
let snapPayStartedAt;
let snapOptions = {};

/**
 * Post Message Listener
 * @param Event:event
 */
function messageHandler(event) {
  if (event.origin !== BASE_URL) {
    return;
  }

  const eventData = event.data;

  switch (eventData.event) {
    case 'hide':
      if (snapApp.isVisible() && snapOptions.onClose) {
        snapOptions.onClose();
      }

      snap.hide();
      break;
    case 'queryToken':
      if (queuedToken) {
        snapApp.postMessage({
          event: 'token',
          token: queuedToken,
          scriptLoadDuration: getScriptLoadDuration(),
          snapPayStartedAt,
          options: filterNonSerializableOptions(snapOptions),
          isEmbedded: !!snapOptions.embedId,
        });

        if (snapApp.embedded) {
          snapApp.showEmbedded();
        } else {
          snapApp.showPopup();
        }

        currentState = PopupInView;
      }
      break;
    case 'deeplink': {
      const resultData = eventData.data;
      if (resultData) {
        window.location.replace(resultData);
      }
      break;
    }
    case 'result': {
      const resultData = eventData.data;
      const statusCode = resultData.status_code ? String(resultData.status_code) : null;
      const fireCallbacks = function () {
        if (statusCode === '200' && snapOptions.onSuccess) {
          return snapOptions.onSuccess(resultData);
        }

        if (statusCode === '201' && snapOptions.onPending) {
          return snapOptions.onPending(resultData);
        }
        if (statusCode && ['200', '201'].indexOf(statusCode) === -1 && snapOptions.onError) {
          return snapOptions.onError(resultData);
        }
        if (resultData.finish_redirect_url) {
          window.location.href = resultData.finish_redirect_url;
        }
      };

      try {
        fireCallbacks();
      } catch (e) {
        console.error(e);
      }
      snap.hide();
      break;
    }
    case 'debug':
      break;
    default:
      throw new Error('Invalid event data: ' + eventData.event);
  }
}

function typeOf(expectedType) {
  return (key, value) => {
    const actualType = typeof value;
    if (expectedType !== actualType) {
      throw new Error(key + ' should be of type ' + expectedType);
    }
    return true;
  };
}

function array(itemType) {
  return (key, value) => {
    if (Object.prototype.toString.call(value) !== '[object Array]') {
      throw new Error(key + ' should be an array of ' + itemType);
    }
    value.forEach((item) => {
      const actualType = typeof item;
      if (itemType !== actualType) {
        throw new Error(key + ' should be an array of ' + itemType);
      }
    });
    return true;
  };
}

function required(value, key) {
  if (!value) {
    throw new Error(key + ' is required');
  }
}

const popupSpec = {
  onSuccess: typeOf('function'),
  onPending: typeOf('function'),
  onError: typeOf('function'),
  onClose: typeOf('function'),
  skipOrderSummary: typeOf('boolean'),
  autoCloseDelay: typeOf('number'),
  language: typeOf('string'),
  enabledPayments: array('string'),
  skipCustomerDetails: typeOf('boolean'),
  showOrderId: typeOf('boolean'),
  isDemoMode: typeOf('boolean'),
  creditCardNumber: typeOf('string'),
  creditCardCvv: typeOf('string'),
  creditCardExpiry: typeOf('string'),
  customerEmail: typeOf('string'),
  customerPhone: typeOf('string'),
  uiMode: typeOf('string'),
  gopayMode: typeOf('string'),
  selectedPaymentType: typeOf('string'),
};

const embeddedSpec = {
  ...popupSpec,
  embedId: typeOf('string'),
};

function validateOptions(options, specs) {
  for (const key in options) {
    if (!options.hasOwnProperty(key)) {
      continue;
    }

    if (!specs[key]) {
      throw new Error('Unsupported option ' + key);
    }

    const validator = specs[key];
    validator(key, options[key]);
  }
}

function filterNonSerializableOptions(options) {
  const filtered = {};
  for (const key in options) {
    if (typeof options[key] !== 'function') {
      filtered[key] = options[key];
    }
  }
  return filtered;
}

const snap = {
  show: defineTransition('show', () => {
    snapApp.showPopup();
  }, [Initialized], Loading),

  hide: defineTransition('hide', () => {
    snapApp.hide();
    queuedToken = null;
  }, [Initialized, Loading, PopupInView], Initialized),

  init: defineTransition('init', (clientKey) => {
    if (!clientKey) {
      console.log('Please add `data-client-key` attribute in the script tag <script type="text/javascript" src="...snap.js" data-client-key="CLIENT-KEY"></script>, Otherwise custom UI confirguration will not take effect');
    }
    snapApp = new SnapApp(clientKey);
    snapApp.attach();
    window.addEventListener('message', messageHandler, false);
  }, [UnInitialized], Initialized),

  pay: defineTransition('pay', (snapToken, options) => {
    required(snapToken, 'snapToken');
    snapOptions = options || {};
    validateOptions(snapOptions, popupSpec);
    queuedToken = snapToken;
    snapPayStartedAt = +new Date();
    snapApp.attach();
    if (snapApp.attached) {
      snapApp
        .goHome()
        .postMessage({
          event: 'notify'
        });
    }
  }, [Initialized, Loading], PopupInView),

  embed: defineTransition('embed', (snapToken, options) => {
    required(snapToken, 'snapToken');
    required(options?.embedId, 'embedId');

    snapOptions = options || {};
    validateOptions(snapOptions, embeddedSpec);

    queuedToken = snapToken;
    snapPayStartedAt = +new Date();

    snapApp.attach(options.embedId);

    if (snapApp.attached) {
      snapApp
        .goHome()
        .postMessage({
          event: 'notify'
        });
    }
  }, [Initialized, Loading], PopupInView),

  reset: defineTransition('reset', () => {
    if (snapApp) {
      snapApp.detach();
      snapApp = null;
    }

    window.removeEventListener('message', messageHandler, false);
    parentStyle = undefined;
    queuedToken = null;
  }, [Initialized, UnInitialized, Loading, PopupInView], UnInitialized)
};

const scriptTag = getSnapScript();
const clientKey = (scriptTag && scriptTag.getAttribute('data-client-key')) || '';

if (process.env.NODE_ENV !== 'test') {
  snap.init(clientKey);
  (function readyStateCheck() {
    if (document.readyState === 'complete') {
      if (snapApp) {
        snapApp.attach();
      }
    } else {
      setTimeout(readyStateCheck, 100);
    }
  })();
}

window.snap = snap;
